@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.slick-slider .slider-arrow {
  position: absolute;
  top: 50%;
  background: none !important;
  border: none;
  color: #580f11;
  z-index: 40; }
  .slick-slider .slider-arrow:hover {
    cursor: pointer; }
  .slick-slider .slider-arrow.right {
    right: -1.5rem; }
  .slick-slider .slider-arrow.left {
    left: -1.5rem; }
  @media (max-width: 950px) {
    .slick-slider .slider-arrow {
      display: none !important; } }

.slick-slider .slick-dots .slick-active div {
  background-color: #feab54 !important;
  -webkit-transition: all .5s ease;
  transition: all .5s ease; }
  .slick-slider .slick-dots .slick-active div:hover {
    cursor: pointer; }

@-webkit-keyframes bounceCustom {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  6% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  12% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  18% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  24% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes bounceCustom {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  6% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  12% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  18% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  24% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 1.2rem; }
  @media (max-width: 1400px) {
    * {
      font-size: 1rem; } }

body, html, main, #root {
  background: #efefff;
  overflow-x: hidden;
  width: 100vw; }

section {
  padding: 5rem 7rem 5rem 9rem;
  width: 100%; }
  @media (max-width: 768px) {
    section#por-que-elegirnos {
      padding: 4rem 0 0; } }
  section .container {
    width: 100%; }
  @media (max-width: 960px) {
    section {
      padding: 4rem;
      padding-left: 5rem; } }
  @media (max-width: 768px) {
    section {
      padding: 4rem 0 2rem; }
      section > .container > h1 {
        padding-right: 1.5rem;
        padding-left: 1.5rem; } }

h1.section-title {
  margin-bottom: 4rem;
  font-size: 3rem;
  letter-spacing: 2px;
  font-weight: 900;
  position: relative;
  display: inline-flex;
  justify-content: center; }
  h1.section-title::after {
    content: '';
    position: absolute;
    width: 30%;
    height: 5px;
    background: #580f11;
    bottom: -1rem;
    right: 0; }
  @media (max-width: 768px) {
    h1.section-title {
      text-align: center !important;
      font-size: 2rem;
      display: flex; }
      h1.section-title::after {
        right: auto; } }

b {
